body {
  overflow: hidden;
}

.wrapper {
  color: #000;
  background: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, 'Segoe UI',
    'Fira Sans', Avenir, 'Helvetica Neue', 'Lucida Grande', sans-serif;
  height: 100vh;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

h1 {
  color: #898989;
  font-size: 5vw;
  font-weight: 500;
}

.container {
  width: 80vw;
  margin: auto;
  margin-top: 10vw;
  text-align: center;
}

.earth,
.moon {
  border: 0.5vw solid #6fd2e8;
  width: 15vw;
  height: 15vw;
  border-radius: 9vw;
  text-align: center;
  display: inline-block;
}

.earth span,
.moon span {
  font-family: 'Helvetica', sans-serif;
  color: #6fd2e8;
  font-weight: 700;
  line-height: 15vw;
  font-size: 2vw;
}

.distance {
  border-top: 0.3vw dashed #aaa;
  margin: 0 1vw;
  display: inline-block;
  width: 0;
  -webkit-transform: translateY(1.8vw);
      -ms-transform: translateY(1.8vw);
          transform: translateY(1.8vw);

  -webkit-animation-duration: 5s;

          animation-duration: 5s;
  -webkit-animation-name: distance;
          animation-name: distance;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.distance-label {
  font-family: 'Helvetica', sans-serif;
  color: #898989;
  font-weight: 700;
  font-size: 3vw;
  text-align: center;

  display: block;
  width: 100%;
  -webkit-transform: translateY(-5vw);
      -ms-transform: translateY(-5vw);
          transform: translateY(-5vw);
}

.moon {
  border-color: #aaa;
  width: 8vw;
  height: 8vw;
}

.moon span {
  color: #aaa;
  line-height: 8vw;
}

@-webkit-keyframes distance {
  from {
    width: 8vw;
  }

  to {
    width: 49vw;
  }
}

@keyframes distance {
  from {
    width: 8vw;
  }

  to {
    width: 49vw;
  }
}

.monster {
  position: absolute;
  bottom: -100%;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 70vh;

  -o-transition: bottom 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);

  transition: bottom 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  -webkit-transition: bottom 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.monster-animate {
  bottom: -10vh;
}

#gremlin {
  -webkit-transform: translate(0px, 12px);
      -ms-transform: translate(0px, 12px);
          transform: translate(0px, 12px);
}
#shoulder {
  -webkit-transform-origin: 380px 492px;
      -ms-transform-origin: 380px 492px;
          transform-origin: 380px 492px;
  -webkit-animation: shoulder 2s ease-in-out infinite;
          animation: shoulder 2s ease-in-out infinite;
  position: relative;
}
#forearm {
  -webkit-transform-origin: 148px 382px;
      -ms-transform-origin: 148px 382px;
          transform-origin: 148px 382px;
  -webkit-animation: elbow 2s ease-in-out infinite;
          animation: elbow 2s ease-in-out infinite;
  position: relative;
}
#hand {
  -webkit-transform-origin: 166px 166px;
      -ms-transform-origin: 166px 166px;
          transform-origin: 166px 166px;
  -webkit-animation: wrist 2s ease-in-out infinite;
          animation: wrist 2s ease-in-out infinite;
  position: relative;
}
#brow {
  -webkit-transform-origin: 490px 210px;
      -ms-transform-origin: 490px 210px;
          transform-origin: 490px 210px;
  -webkit-animation: scrunch 6s ease-in-out infinite;
          animation: scrunch 6s ease-in-out infinite;
  position: relative;
}
#ear {
  -webkit-transform-origin: 576px 168px;
      -ms-transform-origin: 576px 168px;
          transform-origin: 576px 168px;
  -webkit-animation: scrunch 4s ease-in-out infinite;
          animation: scrunch 4s ease-in-out infinite;
  position: relative;
}
#body {
  -webkit-transform-origin: 450px 712px;
      -ms-transform-origin: 450px 712px;
          transform-origin: 450px 712px;
  -webkit-animation: sway 2s ease-in-out infinite;
          animation: sway 2s ease-in-out infinite;
  position: relative;
}
#anchor {
  -webkit-transform-origin: 596px 552px;
      -ms-transform-origin: 596px 552px;
          transform-origin: 596px 552px;
  position: absolute;
}
@-webkit-keyframes shoulder {
  50% {
    -webkit-transform: rotate(24deg);
            transform: rotate(24deg);
  }
}
@keyframes shoulder {
  50% {
    -webkit-transform: rotate(24deg);
            transform: rotate(24deg);
  }
}
@-webkit-keyframes elbow {
  50% {
    -webkit-transform: rotate(14deg);
            transform: rotate(14deg);
  }
}
@keyframes elbow {
  50% {
    -webkit-transform: rotate(14deg);
            transform: rotate(14deg);
  }
}
@-webkit-keyframes wrist {
  50% {
    -webkit-transform: rotate(8deg);
            transform: rotate(8deg);
  }
}
@keyframes wrist {
  50% {
    -webkit-transform: rotate(8deg);
            transform: rotate(8deg);
  }
}
@-webkit-keyframes sway {
  50% {
    -webkit-transform: rotate(1deg);
            transform: rotate(1deg);
  }
}
@keyframes sway {
  50% {
    -webkit-transform: rotate(1deg);
            transform: rotate(1deg);
  }
}
@-webkit-keyframes scrunch {
  0%,
  71% {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
  }
  72%,
  73% {
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
  }
  74%,
  100% {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
  }
}
@keyframes scrunch {
  0%,
  71% {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
  }
  72%,
  73% {
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
  }
  74%,
  100% {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
  }
}
@-webkit-keyframes wiggle {
  0%,
  23% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  24%,
  100% {
    -webkit-transform: rotate(4deg);
            transform: rotate(4deg);
  }
}
@keyframes wiggle {
  0%,
  23% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  24%,
  100% {
    -webkit-transform: rotate(4deg);
            transform: rotate(4deg);
  }
}
svg {
  width: 80%;
}




